#new-login{
    width: 80%;
    height: 550px;
    background-color: skyblue;
    margin-left: 10%;
    position: relative;
}

.new-div{
    position: absolute;
    width: 50%;
    height: 80%;
    background-color: aliceblue;
    top: 10%;
    left: 25%;
}

.new-div h2{
    line-height: 100px;
    text-align: center;
}

.new-id, .new-pass, .new-name{
    width: 100%;
    height: 40px;
    text-align: center;
}

.new-id span, .new-pass span, .new-name span{
    float: left;
    margin-left: 20%;
}

.new-id input, .new-pass input, .new-name input{
    float: right;
    margin-right: 20%;
}

.new-btn{
    width: 100%;
    height: 60px;
}

.new-btn button{
    margin-top: 2%;
    margin-left: 80%;
    background-color: greenyellow;
    border: 1px solid green;
    color: green;
    font-weight: bold;
    cursor: pointer;
}

.new-btn button:hover{
    opacity: 0.7;
}

.new-div hr{
    margin-top: 20px;
}

.new-txt{
    width: 100%;
    line-height: 80px;
    text-align: center;
    color: red;
}

@media (max-width: 800px) {
    .new-div{
        position: absolute;
        width: 80%;
        height: 80%;
        background-color: aliceblue;
        top: 10%;
        left: 10%;
    }
    
    .new-btn button{
        margin-top: 5%;
        margin-left: 40%;
    }

    .new-id span, .new-pass span, .new-name span{
        margin-left: 5%;
        width: 80px;
        font-size: 0.8em;
        line-height: 19px;
    }
    
    .new-id input, .new-pass input, .new-name input{
        margin-right: 5%;
        width: 120px;
        font-size: 0.8em;
    }
  }