#board{
    width: 80%;
    height: 550px;
    background-color: skyblue;
    margin-left: 10%;
    position: relative;
}

#board button{
    width: 100px;
    height: 100px;
    border-radius: 0 0 0 50%;
    border: none;
    background-color: aliceblue;
    font-weight: bold;
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 1.2em;
}

#board button:hover{
    background-color: aquamarine;
    color: brown;
}

#board .board-title{
    text-align: center;
    line-height: 50px;
    font-size: 1.4em;
    font-weight: bold;
}

@media (max-width: 800px) {
    #board button{
        width: 50px;
        height: 50px;
    }
  }